.profile__name {
  max-width: 220px;
  margin: 0;
  font-weight: 500;
  font-size: 27px;
  line-height: 33px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media screen and (min-width: 1024px) {
  .profile__name {
    max-width: 340px;
    font-size: 42px;
    line-height: 48px;
  }
}
