.profile__edit-button {
  width: 18px;
  height: 18px;
  border: none;
  background-color: transparent;
  background-image: url(../../../images/Edit-Button.svg);
  background-repeat: no-repeat;
  background-size: contain;
}
@media screen and (min-width: 1024px) {
  .profile__edit-button {
    width: 24px;
    height: 24px;
    margin-top: 8px;
  }
}
