.popup__container {
  width: 282px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 25px 22px;
  box-sizing: border-box;
  position: relative;
}
@media screen and (min-width: 1024px) {
  .popup__container {
    width: 430px;
    padding: 34px 36px 37px;
  }
}
