.profile__pic {
  width: 120px;
  height: 120px;
  position: relative;
}
.profile__pic::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../../../images/edit-pic.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.8);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-out, opacity 0.3s ease-out;
}

.profile__pic:hover::before {
  cursor: pointer;
  opacity: 1;
  visibility: visible;
}
